
    <template>
  <v-app>
    <div id="div1">
      <v-card height="100%" elevation="24">
        <v-navigation-drawer>
          <template v-slot:prepend>
            <v-list-item two-line>
              <v-list-item-avatar>
                <!-- <img src="https://randomuser.me/api/portraits/women/81.jpg" /> -->
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title>{{ username }}</v-list-item-title>
                <v-list-item-subtitle>Logged In</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </template>
          <v-divider></v-divider>
          <v-list rounded>
            <v-subheader></v-subheader>
            <v-list-item-group
              v-model="selectedItem"
              color="green"
              elevation="24"
            >
              <v-list-item v-for="(item, i) in items" :key="i">
                <v-list-item-icon>
                  <v-icon v-text="item.icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title
                    v-text="item.title"
                    @click="goToPage(item.title)"
                  ></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-navigation-drawer>
      </v-card>
    </div>

    <!-- <v-app-bar app> -->

    <!-- </v-app-bar> -->

    <!-- Sizes your content based upon application components -->
    <div id="div2">
      <v-main>
        <!-- Provides the application the proper gutter -->
        <div id="main">
          <template>
            <div class="row">
              <div class="col-md-6">
                <v-card id="charts" class="card card-chart">
                  <highcharts
                    :options="chartOptions1"
                    :highcharts="graph1"
                  ></highcharts>
                </v-card>
              </div>

              <div id="charts" class="col-md-6">
                <v-card class="card card-chart">
                  <!-- <div class="card-header card-header-danger"> -->
                  <highcharts
                    :options="chartOptions4"
                    :highcharts="graph4"
                  ></highcharts>

                  <!-- </div> -->
                </v-card>
              </div>
            </div>
            <div class="row">
              <div id="charts" class="col-md-12">
                <v-card class="card card-chart" id="graph2">
                  <highcharts
                    :options="chartOptions3"
                    :highcharts="graph3"
                  ></highcharts>

                  <!-- <img v-if="images != null" width="100%" :src="images['final_predictions']" alt="img" /> -->
                  <!-- <v-img :src="images['final_predictions']"></v-img> -->
                </v-card>
                <!-- <v-img
                    src="image"
                    height="500"
                  ></v-img> -->
              </div>
            </div>
            <div class="row">
              <div id="charts" class="col-md-12">
                <v-card class="card card-chart" id="graph2">
                  <highcharts
                    :options="chartOptions5"
                    :highcharts="graph5"
                  ></highcharts>
                </v-card>
              </div>
            </div>
            <!-- <div clas="row"></div>
            <div class="row"></div> -->
          </template>
        </div>
      </v-main>
    </div>
  </v-app>
</template>

<script>
import Highcharts from "highcharts";
import axios from "axios";
import dataModule from "highcharts/modules/data";

import HC_exportData from "highcharts/modules/export-data";
HC_exportData(Highcharts);

dataModule(Highcharts);

export default {
  props: ["username", "results"],
  data() {
    return {
      image: "",

      errors: [],
      csvdata: null,
      have_results: "none",
      images: null,
      updatedImages: [],
      selectedItem: "",
      mySeries: [10, 20, 30, 40, 50, 60],

      items: [
        { title: "Dashboard", icon: "mdi-view-dashboard" },
        { title: "Input Data", icon: "mdi-forum" },
        { title: "Logout", icon: "mdi-exit-to-app" },
      ],

      graph1: Highcharts,
      chartOptions1: {
        plotOptions: {
          series: {
            // marker: {
            //   enabled: false,
            // },
          },
        },

        data: {
          csv: "",
        },

        credits: {
          enabled: false,
        },

        chart: {
          type: "line",
        },
        title: {
          text: "Yearly Sales Trend",
        },

        subtitle: {
          text: "",
        },
        yAxis: {
          title: {
            text: "Number of Sales",
          },
        },
      },
      graph2: Highcharts,
      chartOptions2: {
        plotOptions: {
          series: {
            marker: {
              enabled: false,
            },
          },
        },
        series: [
          {
            lineWidth: 1,
          },
          {
            type: "areaspline",
            color: "#c4392d",
            negativeColor: "#5679c4",
            fillOpacity: 0.5,
          },
        ],
        data: {
          csv: "",

          complete: function (options) {
            options.series.push({
              name: "Trend",
              data: [
                { x: 1880, y: -0.4 },
                { x: 2014, y: 0.52 },
              ],
            });
          },

          enablePolling: true,
        },
        chart: {
          type: "spline",

          plotBorderColor: "black",
        },
        title: {
          text: "Global temperature change",
        },

        subtitle: {
          text:
            "Data module: options structure is modified before final rendering",
        },
      },
      graph3: Highcharts,
      chartOptions3: {
        plotOptions: {
          series: {
            color: "#318282",
          },
        },
        chart: {
          type: "line",
        },

        data: {
          csv: "",
        },
        credits: {
          enabled: false,
        },
        title: {
          text: "Store performance",
        },
        yAxis: {
          title: {
            text: "Number of sales",
          },
        },
        xAxis: {
          type: "datetime",
        },
        series: [
          {
            type: "column",

            color: "#42c7aa",
          },
          {
            type: "line",
          },
        ],

        tooltip: {
          crosshairs: true,
          shared: true,
        },
      },
      graph4: Highcharts,
      chartOptions4: {
        plotOptions: {
          series: {
            color: "#ffa500",
          },
        },
        chart: {
          type: "line",
        },
        credits: {
          enabled: false,
        },

        data: {
          csv: "",
        },

        yAxis: {
          title: {
            text: "Number of sales",
          },
        },
        title: {
          text: "Sales prediction",
        },
      },
      graph5: Highcharts,
      chartOptions5: {
        plotOptions: {
          series: {
            color: "#273e60",
          },
        },
        chart: {
          type: "line",
        },
        data: {
          csv: "",
        },

        yAxis: {
          title: {
            text: "Number of transactions",
          },
        },
        title: {
          text: "Transactions per month",
        },
        credits: {
          enabled: false,
        },
      },
    };
  },

  methods: {
    goToPage(title) {
      if (title == "Input Data") {
        this.$router.push({
          name: "InputData",
          params: { username: this.username },
        });
      }
      if (title == "Logout") {
        this.$router.push({
          name: "Home",
        });
      }
    },

    updateData() {
      axios
        .get("backendFinal/prediction.csv")
        .then((response) => {
          this.chartOptions4.data.csv = response.data;
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
      axios
        .get("backendFinal/sales.csv")
        .then((response) => {
          this.chartOptions1.data.csv = response.data;

          console.log("this is sales.csv ==== " + response.data);
        })
        .catch((error) => {
          console.log(error);
        });
      axios
        .get("backendFinal/storeFile.csv")
        .then((response) => {
          this.chartOptions3.data.csv = response.data;

          console.log("this is storeFile.csv ==== " + response.data);
        })
        .catch((error) => {
          console.log(error);
        });
      axios
        .get("backendFinal/transactions.csv")
        .then((response) => {
          this.chartOptions5.data.csv = response.data;

          console.log("this is transactions.csv ==== " + response.data);
        })
        .catch((error) => {
          console.log(error);
        });
      // this.chartOptions3.data.csv = this.$refs.data1.innerHTML; // This is based on input data of csv data but not from csv file
    },
  },
  mounted: function () {
    this.updateData();
  },
};
</script>

<style>
#div1 {
  height: 100%;
  width: 20%;
  position: fixed;
  left: 0;
}

#div2 {
  width: 80%;
  position: absolute;
  right: 0;
  height: 100%;
  overflow: auto;
}

#main {
  margin: 3%;
}

#completedTasksChart {
  height: 15;
  margin: 10;
}

#graph2 {
  height: 500px;
}
</style>
